import { Link } from "react-router-dom";
import styles from "../../assets/components/ServiceCard.module.css";
import Button from "./Button";
export default function ServiceCard({ heading, innerText, imgURL, linkUrl }) {
  return (
    <div className={styles.ServiceCard}>
      <Link to={linkUrl}>
        <div
          className={styles["ServiceCard__img"]}
          style={{ backgroundImage: `url('${imgURL}')` }}
        ></div>
      </Link>
      <div className={styles["ServiceCard__content"]}>
        <h3>{heading}</h3>
        <p>{innerText}</p>
        <Button
          linkUrl={linkUrl}
          innerText="Learn more"
          color="orange"
          size="small"
        ></Button>
      </div>
    </div>
  );
}
