import { useState } from "react";
import styles from "../../assets/components/ContactForm.module.css";
import Button from "./Button";
import Input from "./Input";
import { database } from "../../firebase";
import { ref, set } from "firebase/database";
import uuid from "uuid-random";

export default function ContactForm() {
  const [formData, setFormData] = useState({
    id: uuid(),
    email: "",
    firstName: "",
    lastName: "",
    phoneNumber: "",
    companyName: "",
    message: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    //    Firebase
    const formRef = ref(database, "contacts/" + formData.id);

    set(formRef, formData)
      .then(() => {
        alert("Data saved successfully!");
      })
      .catch((error) => {
        console.error("Error saving data:", error);
      });
  };

  return (
    <form className={styles.ContactForm} onSubmit={handleSubmit}>
      <div className={styles.contactForm__wrap}>
        <Input
          label="Email*"
          className={styles.form__input}
          type="email"
          name="email"
          id="email"
          required
          placeholder="Email"
          onChange={handleChange}
          value={formData.email}
        />
        <div className={styles.inputWrapper}>
          <Input
            label="First Name*"
            className={styles.form__input}
            type="text"
            name="firstName"
            id="firstName"
            required
            placeholder="First Name"
            onChange={handleChange}
            value={formData.firstName}
          />
          <Input
            label="Last Name"
            className={styles.form__input}
            type="text"
            name="lastName"
            id="lastName"
            placeholder="Last Name"
            onChange={handleChange}
            value={formData.lastName}
          />
        </div>
        <Input
          label="Phone Number*"
          className={styles.form__input}
          type="tel"
          pattern="[0-9]{11}"
          name="phoneNumber"
          id="phoneNumber"
          required
          placeholder="Phone Number"
          onChange={handleChange}
          value={formData.phoneNumber}
        />
        <Input
          label="Company Name"
          className={styles.form__input}
          type="text"
          name="companyName"
          id="companyName"
          placeholder="Company"
          onChange={handleChange}
          value={formData.companyName}
        />
        <label className={styles.form__label}>
          Message
          <textarea
            className={`${styles.form__input} ${styles["form__input--message"]}`}
            type="text"
            name="message"
            id="message"
            placeholder="Enter your message here..."
            onChange={handleChange}
          />
        </label>
      </div>
      <Button typeSubmit innerText="get Started Now" size="extraLarge" />
    </form>
  );
}
