import styles from "../../assets/components/Footer.module.css";
import Button from "./Button";
import logo from "../../assets/img/eventify-logo.svg";
import twitterSvg from "../../assets/img/ant-design_x-outlined.svg";
import facebookSvg from "../../assets/img/ic_baseline-facebook.svg";
import youtubeSvg from "../../assets/img/mdi_youtube.svg";
import instagramSvg from "../../assets/img/mdi_instagram.svg";
import telegramSvg from "../../assets/img/ic_baseline-telegram.svg";
import { Link } from "react-router-dom";
import SocialMedia from "./SocialMedia";
import Input from "./Input";
import { database } from "../../firebase";
import { ref, set } from "firebase/database";
import { useState } from "react";
import uuid from "uuid-random";

export default function Footer() {
  const [formData, setFormData] = useState({
    id: uuid(),
    emailSubscribe: "",
    termsAgreed: true,
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    //    Firebase
    const formRef = ref(database, "subscription/" + formData.id);

    set(formRef, formData)
      .then(() => {
        alert("Data saved successfully!");
      })
      .catch((error) => {
        console.error("Error saving data:", error);
      });
  };
  return (
    <footer className={styles.Footer}>
      <div className={styles.Footer__colWrapper}>
        <div className={`${styles.col} ${styles["col--info"]}`}>
          <img className={styles.info__logo} src={logo} alt="Eventify" />
          <p className={styles.info__p}>
            Professional event planning services to&nbsp;make your special day
            perfect
          </p>
        </div>
        <ul className={`${styles.col} ${styles["col--links"]}`}>
          <Link className={styles.links__link} to="/home">
            <li className={styles.links__item}>Home</li>
          </Link>
          <Link className={styles.links__link} to="/services">
            <li className={styles.links__item}>Services</li>
          </Link>
          <Link className={styles.links__link} to="/about-us">
            <li className={styles.links__item}>About us</li>
          </Link>
          <Link className={styles.links__link} to="/contact-us">
            <li className={styles.links__item}>Contacts</li>
          </Link>
        </ul>
        <div className={`${styles.col} ${styles["col--newsletter"]}`}>
          <form className={styles.subscription__form} onSubmit={handleSubmit}>
            <h4 className={styles.subscription__h4}>Stay Updated</h4>
            <Input
              className={styles.subscription__input}
              rounded
              type="email"
              name="emailSubscribe"
              id="emailSubscribe"
              placeholder="Email"
              required
              small
              onChange={handleChange}
            />

            <label className={styles["newsletter__checkboxContainer"]}>
              <input type="checkbox" required />
              <span className={styles.checkmark}></span>
              <p>
                I&nbsp;accept the terms and conditions and consent to&nbsp;the
                processing of&nbsp;my&nbsp;personal data in&nbsp;accordance with
                the privacy policy
              </p>
            </label>
            <Button
              innerText="Subscribe"
              fullWidth={true}
              color="orange"
              typeSubmit
              size="small"
            ></Button>
          </form>
        </div>
        <div className={`${styles.col} ${styles["col--contact"]}`}>
          <h4 className={styles.contact__h4}>Call us</h4>
          <a className={styles["contact__tel"]} href="tel:+31112223344">
            +3 111 222-33-44
          </a>
          <div className={styles["contact__socialWrapper"]}>
            <SocialMedia
              imgUrl={facebookSvg}
              linkUrl="https://www.facebook.com"
            ></SocialMedia>
            <SocialMedia
              imgUrl={instagramSvg}
              linkUrl="https://www.instagram.com"
            ></SocialMedia>
            <SocialMedia
              imgUrl={telegramSvg}
              linkUrl="https://www.t.me"
            ></SocialMedia>
            <SocialMedia
              imgUrl={twitterSvg}
              linkUrl="https://www.x.com"
            ></SocialMedia>
            <SocialMedia
              imgUrl={youtubeSvg}
              linkUrl="https://www.youtube.com"
            ></SocialMedia>
          </div>
        </div>
      </div>
      <p className={styles.Footer__copyright}>
        &copy;Copyright. All rights reserved.
      </p>
    </footer>
  );
}
