import React, { StrictMode } from "react";
import ReactDOM from "react-dom/client";
import "./assets/index.css";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import Home from "./pages/Home/Home";
import ErrorPage from "./pages/ErrorPages/ErrorPage";
import Weddings from "./pages/Services/Weddings";
import Parties from "./pages/Services/Parties";
import Corporate from "./pages/Services/Corporate";
import AllServices from "./pages/Services/AllServices";
import AboutUs from "./pages/About Us/AboutUs";
import ChildParties from "./pages/Services/ChildParties";
import ContactUs from "./pages/ContactUs/ContactUs";

const router = createBrowserRouter([
  {
    path: "/home",
    element: <Home />,
    // errorElement: <ErrorPage />,
    errorElement: <Home />,
  },
  {
    path: "/services",
    element: <AllServices />,
  },
  {
    path: "/about-us",
    element: <AboutUs />,
  },
  {
    path: "/contact-us",
    element: <ContactUs />,
  },
  {
    path: "/services/weddings",
    element: <Weddings />,
  },
  {
    path: "/services/parties",
    element: <Parties />,
  },
  {
    path: "/services/corporate",
    element: <Corporate />,
  },
  {
    path: "/services/child-parties",
    element: <ChildParties />,
  },
]);

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);
