import styles from "../../assets/components/ExtraServiceCard.module.css";
import PropTypes from "prop-types";
import classNames from "classnames";

export default function ExtraServiceCard({
  heading,
  innerText,
  imgURL,
  bgcolor = "blue",
  tel = "",
}) {
  const extraServiceCardClass = classNames(styles.ExtraServiceCard, {
    [styles[`ExtraServiceCard--${bgcolor}`]]: bgcolor,
  });

  const cardContent = (
    <div className={extraServiceCardClass}>
      <img className={styles.icon} src={imgURL} alt="" />
      <div className={styles.ExtraServiceCard__content}>
        <h4>{heading}</h4>
        <p>{innerText}</p>
      </div>
    </div>
  );

  return tel ? <a className={styles.link} href={`tel:${tel}`}>{cardContent}</a> : cardContent;
}

ExtraServiceCard.propTypes = {
  bgcolor: PropTypes.oneOf(["blue", "orange"]),
};
