import styles from "../../assets/components/TeamMemberCard.module.css";
export default function TeamMemberCard({ name, job, desc, imgURL }) {
  return (
    <div className={styles.TeamMemberCard}>
      <img src={imgURL} className={styles.TeamMemberCard__img} alt={name} />
      <div className={styles.TeamMemberCard__content}>
        <h3 className={styles.TeamMemberCard__h3}>{name}</h3>
        <h4 className={styles.TeamMemberCard__h4}>{job}</h4>
        <p className={styles.TeamMemberCard__p}>{desc}</p>
      </div>
    </div>
  );
}
