import Header from "../../components/ui/Header";
import Footer from "../../components/ui/Footer";
import styles from "../../assets/pages/ContactUs/ContactUs.module.css";
import ContactForm from "../../components/ui/ContactForm";
import Button from "../../components/ui/Button";
import locationIcon from "../../assets/img/contactDetails--location.svg";
import clocksIcon from "../../assets/img/contactDetails--clocks.svg";
import phoneIcon from "../../assets/img/contactDetails--phone.svg";
import emailIcon from "../../assets/img/contactDetails--email.svg";
import SocialMedia from "../../components/ui/SocialMedia";
import twitterSvg from "../../assets/img/ant-design_x-outlined.svg";
import facebookSvg from "../../assets/img/ic_baseline-facebook.svg";
import youtubeSvg from "../../assets/img/mdi_youtube.svg";
import instagramSvg from "../../assets/img/mdi_instagram.svg";
import telegramSvg from "../../assets/img/ic_baseline-telegram.svg";
import Input from "../../components/ui/Input";
import { database } from "../../firebase";
import { ref, set } from "firebase/database";
import { useState } from "react";
import uuid from "uuid-random";

export default function ContactUs() {
  const [formData, setFormData] = useState({
    id: uuid(),
    emailSubscribe: "",
    termsAgreed: true,
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const formRef = ref(database, "subscription/" + formData.id);

    set(formRef, formData)
      .then(() => {
        alert("Data saved successfully!");
      })
      .catch((error) => {
        console.error("Error saving data:", error);
      });
  };

  return (
    <div className={styles.ContactUsPage}>
      <Header></Header>
      <section
        className={`${styles.ContactUsPage__banner} ${styles["ContactUsPage__section"]}`}
      >
        <div className={styles.section__heading}>
          <h2 className={styles.section__h2}>contact&nbsp;us</h2>
          <p className={`${styles.section__p} ${styles["section__p--banner"]}`}>
            We&rsquo;d love to&nbsp;hear from you! Reach out to&nbsp;us through
            the form below or&nbsp;via our contact details, and we&rsquo;ll get
            back to&nbsp;you as&nbsp;soon as&nbsp;possible
          </p>
        </div>
      </section>
      <section
        className={`${styles.ContactUsPage__section} ${styles["ContactUsPage__contactUs"]}`}
      >
        <div className={styles.contactUs__contentWrap}>
          <div className={styles.contactFormWrap}>
            <ContactForm></ContactForm>
          </div>
          <div className={styles.content}>
            <div className={styles.contentSection}>
              <h3 className={styles.content__h3}>Stay Updated with Eventify</h3>
              <p className={styles.content__p}>
                Subscribe to&nbsp;our newsletter and be&nbsp;the first
                to&nbsp;know about our latest events, special offers, and expert
                tips for planning unforgettable celebrations
              </p>
              <form
                className={styles.subscription__form}
                onSubmit={handleSubmit}
              >
                <Input
                  label="Email"
                  className={styles.subscription__input}
                  type="email"
                  name="emailSubscribe"
                  id="emailSubscribe"
                  placeholder="Email"
                  required
                  onChange={handleChange}
                />
                <Button typeSubmit innerText="Subscribe" size="large" />
                <p className={styles.subscription__addText}>
                  I&nbsp;accept the terms and conditions and consent to&nbsp;the
                  processing of&nbsp;my&nbsp;personal data in&nbsp;accordance
                  with the privacy policy
                </p>
              </form>
            </div>
            <div className={styles.contentSection}>
              <h3 className={styles.content__h3}>
                Follow Us&nbsp;on&nbsp;Social Media
              </h3>
              <p className={styles.content__p}>
                Stay connected with&nbsp;us on&nbsp;social media for the latest
                updates, behind-the-scenes looks, and exclusive content.
                Follow&nbsp;us and join our community!
              </p>
              <div className={styles.socialWrap}>
                <SocialMedia
                  size="regular"
                  imgUrl={facebookSvg}
                  linkUrl="https://www.facebook.com"
                ></SocialMedia>
                <SocialMedia
                  size="regular"
                  imgUrl={instagramSvg}
                  linkUrl="https://www.instagram.com"
                ></SocialMedia>
                <SocialMedia
                  size="regular"
                  imgUrl={telegramSvg}
                  linkUrl="https://www.t.me"
                ></SocialMedia>
                <SocialMedia
                  size="regular"
                  imgUrl={twitterSvg}
                  linkUrl="https://www.x.com"
                ></SocialMedia>
                <SocialMedia
                  size="regular"
                  imgUrl={youtubeSvg}
                  linkUrl="https://www.youtube.com"
                ></SocialMedia>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className={styles.ContactUsPage__section}>
        <div className={styles.contactDetails__contentWrap}>
          <iframe
            className={styles.contactDetails__map}
            src="https://www.google.com/maps/embed?pb=!1m10!1m8!1m3!1d71368.44770783334!2d-122.42358174475225!3d37.76496658168761!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sru!4v1725126792720!5m2!1sen!2sru"
            allowFullScreen=""
            frameBorder="0"
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
            title="eventify-location"
          ></iframe>
          <div className={styles.contactDetails__wrap}>
            <h3 className={styles.content__h3}>Contact Details</h3>
            <div className={styles.contactDetails__infoWrap}>
              <img
                className={styles.contactDetails__icon}
                src={locationIcon}
                alt=""
              />
              <div className={styles.contactDetails__textWrap}>
                <p className={styles.content__p}>Address</p>
                <p className={styles.content__p}>
                  123 Eventify Street, City, Country
                </p>
              </div>
            </div>
            <div className={styles.contactDetails__infoWrap}>
              <img
                className={styles.contactDetails__icon}
                src={clocksIcon}
                alt=""
              />
              <div className={styles.contactDetails__textWrap}>
                <p className={styles.content__p}>Business Hours</p>
                <p className={styles.content__p}>
                  Monday&nbsp;&mdash; Friday: 9:00&nbsp;AM&nbsp;&mdash;
                  6:00&nbsp;PM
                </p>
              </div>
            </div>
            <div className={styles.contactDetails__infoWrap}>
              <img
                className={styles.contactDetails__icon}
                src={phoneIcon}
                alt=""
              />
              <div className={styles.contactDetails__textWrap}>
                <p className={styles.content__p}>Phone</p>
                <p className={styles.content__p}>+3 111 222-33-44</p>
              </div>
            </div>
            <div className={styles.contactDetails__infoWrap}>
              <img
                className={styles.contactDetails__icon}
                src={emailIcon}
                alt=""
              />
              <div className={styles.contactDetails__textWrap}>
                <p className={styles.content__p}>Email</p>
                <p className={styles.content__p}>info@eventify.com</p>
              </div>
            </div>
          </div>
        </div>
      </section>
      <div className={styles.waves}>
        <svg
          data-name="Layer 1"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1200 120"
          preserveAspectRatio="none"
        >
          <path
            d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z"
            opacity=".25"
            className={styles["shape-fill"]}
          ></path>
          <path
            d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z"
            opacity=".5"
            className={styles["shape-fill"]}
          ></path>
          <path
            d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z"
            className={styles["shape-fill"]}
          ></path>
        </svg>
      </div>
      <Footer></Footer>
    </div>
  );
}
