import Header from "../../components/ui/Header";
import Footer from "../../components/ui/Footer";
import ServiceCard from "../../components/ui/ServiceCard";
import weddingImg from "../../assets/img/home__services--1.png";
import partyImg from "../../assets/img/home__services--2.png";
import chidrenPartyImg from "../../assets/img/home__services--3.png";
import corporateImg from "../../assets/img/home__services--4.png";
import styles from "../../assets/pages/Services/AllServices.module.css";
import ExtraServiceCard from "../../components/ui/ExtraServiceCard";
import FAQCard from "../../components/ui/FAQCard";
import ContactForm from "../../components/ui/ContactForm";
import services_icon_1 from "../../assets/img/extraServices--house.svg";
import services_icon_2 from "../../assets/img/extraServices--dress.svg";
import services_icon_3 from "../../assets/img/extraServices--food.svg";
import services_icon_4 from "../../assets/img/extraServices--drink.svg";
import services_icon_5 from "../../assets/img/extraServices--music.svg";
import services_icon_6 from "../../assets/img/extraServices--photo.svg";
import services_icon_7 from "../../assets/img/extraServices--sun.svg";
import services_icon_8 from "../../assets/img/extraServices--phone.svg";
import services_icon_9 from "../../assets/img/extraServices--envelope.svg";

export default function AllServices() {
  return (
    <div className={styles.MainServicesPage}>
      <Header></Header>
      <section className={styles.MainServicesPage__services}>
        <h2 className={styles.services__h2}>Our Services</h2>
        <div className={styles["services__content"]}>
          <ServiceCard
            linkUrl="/services/weddings"
            heading="Wedding planning"
            innerText="From intimate ceremonies to&nbsp;grand celebrations, our wedding planning service ensures 
            your special day is&nbsp;perfect"
            imgURL={weddingImg}
          ></ServiceCard>
          <ServiceCard
            linkUrl="/services/parties"
            heading="Parties"
            innerText="Create unforgettable parties with our expert planning; we&nbsp;handle every detail so&nbsp;you can celebrate fully"
            imgURL={partyImg}
          ></ServiceCard>
          <ServiceCard
            linkUrl="/services/child-parties"
            heading="Children&rsquo;s Parties"
            innerText="From magical themes to&nbsp;fun activities, we&nbsp;create unforgettable moments for your little one&rsquo;s special day"
            imgURL={chidrenPartyImg}
          ></ServiceCard>
          <ServiceCard
            linkUrl="/services/corporate"
            heading="Corporate Events"
            innerText="Seamless corporate events with our expert planning, managing conferences, seminars, and team-building to&nbsp;meet your goals"
            imgURL={corporateImg}
          ></ServiceCard>
        </div>
      </section>
      <section className={styles.MainServicesPage__extra}>
        <div className={styles.extra__heading}>
          <h2 className={styles.extra__h2}>Extra services</h2>
          <p className={styles.extra__p}>
            We&nbsp;offer all the services that you might need in&nbsp;order
            to&nbsp;have a&nbsp;successful event
          </p>
        </div>
        <div className={styles.extra__cards}>
          <ExtraServiceCard
            heading="Venue"
            innerText="Recommendations and design of&nbsp;event space"
            imgURL={services_icon_1}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Clothing"
            innerText="Hair and makeup professionals"
            imgURL={services_icon_2}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Food"
            innerText="Catering and cake tasting"
            imgURL={services_icon_3}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Beverage"
            innerText="Wine, cocktail and soft drinks selection"
            imgURL={services_icon_4}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Music"
            innerText="Popular artists and bands"
            imgURL={services_icon_5}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Filming"
            innerText="Photography and film professionals"
            imgURL={services_icon_6}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Lightning"
            innerText="Professional lights"
            imgURL={services_icon_7}
          ></ExtraServiceCard>
          <ExtraServiceCard
            tel = "+1234567890"
            bgcolor="orange"
            heading="24/7 support"
            innerText="We&nbsp;are always on&nbsp;call!"
            imgURL={services_icon_8}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Invitations"
            innerText="Design invitations"
            imgURL={services_icon_9}
          ></ExtraServiceCard>
        </div>
      </section>
      <section className={styles.MainServicesPage__faqs}>
        <div className={styles.faqs__heading}>
          <h2 className={styles.faqs__h2}>FAQs</h2>
          <p className={styles.faqs__p}>
            Take a&nbsp;look at&nbsp;what our customers usually ask&nbsp;us when
            they first get in&nbsp;touch with&nbsp;us
          </p>
        </div>
        <div className={styles.FAQCardsWrapper}>
          <FAQCard
            questionText="What services are included in&nbsp;the wedding planning package?"
            answerText="Our wedding planning package includes venue selection, vendor coordination, d&eacute;cor, catering, entertainment, timeline creation, and day-of coordination to&nbsp;ensure everything runs smoothly."
          ></FAQCard>
          <FAQCard
            questionText="Can I&nbsp;book corporate event planning with specific business needs in&nbsp;mind?"
            answerText="Yes, we&nbsp;offer customized solutions for corporate events tailored to&nbsp;the unique goals and objectives of&nbsp;your business. Whether it&rsquo;s a&nbsp;conference, seminar, or&nbsp;team-building event, we&nbsp;work to&nbsp;ensure that your event aligns with your corporate objectives."
          ></FAQCard>
          <FAQCard
            questionText="How early should&nbsp;I start planning a&nbsp;children&rsquo;s party?"
            answerText="We&nbsp;recommend starting the planning process for a&nbsp;children&rsquo;s party at&nbsp;least 2-3 weeks in&nbsp;advance to&nbsp;allow time for developing a&nbsp;unique theme, booking entertainers, and organizing all the activities. This also provides sufficient time for all necessary preparations."
          ></FAQCard>
          <FAQCard
            questionText="Can you assist with organizing parties for small groups or&nbsp;private events?"
            answerText="Absolutely! We&nbsp;organize parties of&nbsp;all sizes, from small private gatherings to&nbsp;large celebrations. Regardless of&nbsp;the scale of&nbsp;the event, we&nbsp;ensure that every detail is&nbsp;considered and your party will be&nbsp;executed to&nbsp;the highest standard."
          ></FAQCard>
        </div>
      </section>
      <section className={styles.MainServicesPage__contactUs}>
        <div className={styles.contactUs__heading}>
          <h2 className={styles.contactUs__h2}>Contact us</h2>
          <p className={styles.contactUs__p}>
            We&rsquo;d love to&nbsp;hear from you! Whether you have questions
            about our services or&nbsp;want to&nbsp;start planning your next
            event, get in&nbsp;touch with&nbsp;us today
          </p>
        </div>
        <ContactForm></ContactForm>
      </section>
      <div className={styles.waves}>
        <svg
          data-name="Layer 1"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1200 120"
          preserveAspectRatio="none"
        >
          <path
            d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z"
            opacity=".25"
            className={styles["shape-fill"]}
          ></path>
          <path
            d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z"
            opacity=".5"
            className={styles["shape-fill"]}
          ></path>
          <path
            d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z"
            className={styles["shape-fill"]}
          ></path>
        </svg>
      </div>
      <Footer></Footer>
    </div>
  );
}
