import styles from "../../assets/components/GalleryCard.module.css";

export default function GalleryCard({ imgURL }) {
  return (
    <div className={styles.GalleryCard}>
      <div
        className={styles["GalleryCard__img"]}
        style={{ backgroundImage: `url('${imgURL}')` }}
      ></div>
    </div>
  );
}
