import Header from "../../components/ui/Header";
import Footer from "../../components/ui/Footer";
import styles from "../../assets/pages/Services/ServicePage.module.css";
import GalleryCard from "../../components/ui/GalleryCard";
import ExtraServiceCard from "../../components/ui/ExtraServiceCard";
import FAQCard from "../../components/ui/FAQCard";
import ContactForm from "../../components/ui/ContactForm";
import bgImage from "../../assets/img/pageBanner--childParty.jpg";
import video from "../../assets/videos/video--chidrenParty.mp4";
import portfolio_1 from "../../assets/img/ChildrenParty/childreParty_1.jpg";
import portfolio_2 from "../../assets/img/ChildrenParty/childreParty_2.jpg";
import portfolio_3 from "../../assets/img/ChildrenParty/childreParty_3.jpg";
import portfolio_4 from "../../assets/img/ChildrenParty/childreParty_4.jpg";
import portfolio_5 from "../../assets/img/ChildrenParty/childreParty_5.jpg";
import portfolio_6 from "../../assets/img/ChildrenParty/childreParty_6.jpg";
import services_icon_1 from "../../assets/img/extraServices--house.svg";
import services_icon_2 from "../../assets/img/extraServices--dress.svg";
import services_icon_3 from "../../assets/img/extraServices--food.svg";
import services_icon_4 from "../../assets/img/extraServices--drink.svg";
import services_icon_5 from "../../assets/img/extraServices--music.svg";
import services_icon_6 from "../../assets/img/extraServices--photo.svg";
import services_icon_7 from "../../assets/img/extraServices--sun.svg";
import services_icon_8 from "../../assets/img/extraServices--phone.svg";
import services_icon_9 from "../../assets/img/extraServices--envelope.svg";

export default function ChildParties() {
  return (
    <div
      className={styles.ServicePage}
      style={{
        background: `linear-gradient(
    0deg,
    rgba(0, 31, 63, 1) 0%,
    rgba(0, 31, 63, 1) 30%,
    rgba(0, 31, 63, 0) 100%
  ),
  url('${bgImage}') no-repeat bottom center / cover`,
      }}
    >
      <Header></Header>
      <section className={styles.ServicePage__aboutService}>
        <h2 className={styles.aboutService__h2}>parties</h2>
        <div className={styles.aboutService__content}>
          <div className={styles.aboutService__text}>
            <p className={styles.aboutService__p}>
              At&nbsp;Eventify, we&nbsp;know how to&nbsp;make children&rsquo;s
              parties magical. Our planning services cover everything from
              themed decorations to&nbsp;fun activities, making your
              child&rsquo;s special day one they&rsquo;ll never forget.
              We&nbsp;work with you to&nbsp;create a&nbsp;party that reflects
              your child&rsquo;s interests and imagination.
            </p>
            <p className={styles.aboutService__p}>
              From birthday parties to&nbsp;holiday celebrations, we&nbsp;take
              care of&nbsp;every detail to&nbsp;ensure a&nbsp;fun, stress-free
              event. Our goal is&nbsp;to&nbsp;create an&nbsp;environment where
              kids can have the time of&nbsp;their lives, while parents can
              relax and enjoy the day too.
            </p>
          </div>
          <video className={styles.aboutService__video} controls>
            <source src={video} type="video/mp4"></source>
            Your browser does not support the video tag.
          </video>
        </div>
      </section>
      <section className={styles.ServicePage__portfolio}>
        <div className={styles.portfolio__heading}>
          <h2 className={styles.portfolio__h2}>Portfolio</h2>
          <p className={styles.portfolio__p}>
            Take a&nbsp;look at&nbsp;the success stories of&nbsp;our clients
          </p>
        </div>
        <div className={styles["portfolio__content"]}>
          <GalleryCard imgURL={portfolio_1}/>
          <GalleryCard imgURL={portfolio_2}/>
          <GalleryCard imgURL={portfolio_3}/>
          <GalleryCard imgURL={portfolio_4}/>
          <GalleryCard imgURL={portfolio_5}/>
          <GalleryCard imgURL={portfolio_6}/>
        </div>
      </section>
      <section className={styles.ServicePage__extra}>
        <div className={styles.extra__heading}>
          <h2 className={styles.extra__h2}>Extra services</h2>
          <p className={styles.extra__p}>
            We&nbsp;offer all the services that you might need in&nbsp;order
            to&nbsp;have a&nbsp;successful event
          </p>
        </div>
        <div className={styles.extra__cards}>
          <ExtraServiceCard
            heading="Venue"
            innerText="Recommendations and design of&nbsp;event space"
            imgURL={services_icon_1}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Clothing"
            innerText="Hair and makeup professionals"
            imgURL={services_icon_2}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Food"
            innerText="Catering and cake tasting"
            imgURL={services_icon_3}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Beverage"
            innerText="Wine, cocktail and soft drinks selection"
            imgURL={services_icon_4}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Music"
            innerText="Popular artists and bands"
            imgURL={services_icon_5}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Filming"
            innerText="Photography and film professionals"
            imgURL={services_icon_6}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Lightning"
            innerText="Professional lights"
            imgURL={services_icon_7}
          ></ExtraServiceCard>
          <ExtraServiceCard
            bgcolor="orange"
            heading="24/7 support"
            innerText="We&nbsp;are always on&nbsp;call!"
            imgURL={services_icon_8}
          ></ExtraServiceCard>
          <ExtraServiceCard
            heading="Invitations"
            innerText="Design invitations"
            imgURL={services_icon_9}
          ></ExtraServiceCard>
        </div>
      </section>
      <section className={styles.ServicePage__faqs}>
        <div className={styles.faqs__heading}>
          <h2 className={styles.faqs__h2}>FAQs</h2>
          <p className={styles.faqs__p}>
            Take a&nbsp;look at&nbsp;what our customers usually ask&nbsp;us when
            they first get in&nbsp;touch with&nbsp;us
          </p>
        </div>
        <div className={styles.FAQCardsWrapper}>
          <FAQCard
            questionText="What services are included in&nbsp;children&rsquo;s party planning?"
            answerText="Our children&rsquo;s party planning services include themed decorations, interactive games, entertainment, catering, and party favors."
          ></FAQCard>
          <FAQCard
            questionText="How far in&nbsp;advance should we&nbsp;book a&nbsp;children&rsquo;s party?"
            answerText="It&rsquo;s best to&nbsp;book at&nbsp;least 1&nbsp;month in&nbsp;advance to&nbsp;ensure we&nbsp;can organize everything perfectly for your child&rsquo;s special day."
          ></FAQCard>
          <FAQCard
            questionText="Can you accommodate specific themes for children&rsquo;s parties?"
            answerText="Yes, we&nbsp;can create parties based on&nbsp;any theme you choose, from superheroes to&nbsp;princesses, ensuring a&nbsp;magical experience for your child."
          ></FAQCard>
          <FAQCard
            questionText="Is&nbsp;there a&nbsp;minimum age requirement for children&rsquo;s parties?"
            answerText="We&nbsp;plan parties for children of&nbsp;all ages, ensuring age-appropriate activities and entertainment for each group."
          ></FAQCard>
        </div>
      </section>
      <section className={styles.ServicePage__contactUs}>
        <div className={styles.contactUs__heading}>
          <h2 className={styles.contactUs__h2}>Contact us</h2>
          <p className={styles.contactUs__p}>
            We&rsquo;d love to&nbsp;hear from you! Whether you have questions
            about our services or&nbsp;want to&nbsp;start planning your next
            event, get in&nbsp;touch with&nbsp;us today
          </p>
        </div>
        <ContactForm></ContactForm>
      </section>
      <div className={styles.waves}>
        <svg
          data-name="Layer 1"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1200 120"
          preserveAspectRatio="none"
        >
          <path
            d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z"
            opacity=".25"
            className={styles["shape-fill"]}
          ></path>
          <path
            d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z"
            opacity=".5"
            className={styles["shape-fill"]}
          ></path>
          <path
            d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z"
            className={styles["shape-fill"]}
          ></path>
        </svg>
      </div>
      <Footer></Footer>
    </div>
  );
}
