import React, { useState } from "react";
import styles from "../../assets/components/CallWidget.module.css";
import phoneIcon from "../../assets/img/callWidget--phone.svg";
import closeIcon from "../../assets/img/callWidget--close.svg";
import Button from "./Button";
import Input from "./Input";
import { database } from "../../firebase";
import { ref, set } from "firebase/database";
import uuid from "uuid-random";

export default function CallWidget() {
  const date = new Date();
  const currentDate = date.toLocaleDateString();
  const currentTime = date.toLocaleTimeString();

  const [formData, setFormData] = useState({
    id: uuid(),
    firstName: "",
    phoneNumber: "",
    requestTime: `${currentDate} ${currentTime}`,
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const formRef = ref(database, "callRequest/" + formData.id);

    set(formRef, formData)
      .then(() => {
        alert("Data saved successfully!");
      })
      .catch((error) => {
        console.error("Error saving data:", error);
      });
  };

  const [isFormOpen, setIsFormOpen] = useState(false);

  const toggleForm = () => {
    setIsFormOpen(!isFormOpen);
  };

  return (
    <>
      <div className={styles.widgetButton} onClick={toggleForm}>
        <img src={phoneIcon} className={styles.phoneIcon} />
      </div>

      {isFormOpen && (
        <div className={styles.formContainer}>
          <div className={styles.formHeader}>
            <h3>Request a Callback</h3>
            <button onClick={toggleForm} className={styles.closeButton}>
              <img src={closeIcon} className={styles.closeIcon} />
            </button>
          </div>
          <form className={styles.form} onSubmit={handleSubmit}>
            <Input
              label="First Name*"
              type="text"
              name="firstName"
              id="firstName"
              required
              placeholder="First Name"
              onChange={handleChange}
            />
            <Input
              label="Phone Number*"
              type="tel"
              name="phoneNumber"
              id="phoneNumber"
              pattern="[0-9]{11}"
              required
              placeholder="Phone Number"
              onChange={handleChange}
            />
            <Button typeSubmit innerText="Submit Request" />
          </form>
        </div>
      )}
    </>
  );
}
