import { Link } from "react-router-dom";
import styles from "../../assets/components/Header.module.css";
import logo from "../../assets/img/eventify-logo.svg";
import hamburgerMenuIcon from "../../assets/img/hamburger-menu.svg";
import { useState } from "react";

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <header className={styles.Header}>
      <Link to="/home">
        <img className={styles.Header__logo} src={logo} alt="Eventify" />
      </Link>
      <ul className={`${styles.Header__navbar} ${isOpen ? styles.open : ""}`}>
        <Link to="/home">
          <li className={styles.link}>Home</li>
        </Link>
        <li className={`${styles.link} ${styles['link--services']}`}>
          <Link to="/services">Services</Link>
          <ul className={styles.dropdownMenu}>
            <Link to="/services/weddings">
              <li className={styles.dropdownLink}>Wedding</li>
            </Link>
            <Link to="/services/parties">
              <li className={styles.dropdownLink}>Party</li>
            </Link>
            <Link to="/services/child-parties">
              <li className={styles.dropdownLink}>Children's party</li>
            </Link>
            <Link to="/services/corporate">
              <li className={styles.dropdownLink}>Corporate</li>
            </Link>
          </ul>
        </li>
        <Link to="/about-us">
          <li className={styles.link}>About Us</li>
        </Link>
        <Link to="/contact-us">
          <li className={styles.link}>Contact</li>
        </Link>
      </ul>
      <div className={styles.hamburgerMenu} onClick={toggleMenu}>
        <img className={styles.hamburger} src={hamburgerMenuIcon} alt="Menu" />
      </div>
    </header>
  );
}
