import styles from "../../assets/components/CommentCard.module.css";
import StarRating from "./StarRating";
export default function CommentCard({ name, place, innerText, imgURL, rate }) {
  return (
    <div className={styles.CommentCard}>
      <div className={styles["CommentCard__content"]}>
        <div className={styles["CommentCard__avatar"]}>
          <img src={imgURL} alt="avatar"
          />
        </div>
        <div className={styles["CommentCard__text"]}>
          <h3>{name}</h3>
          <p>{place}</p>
        </div>
      </div>
      <StarRating rate={rate}></StarRating>
        <p className={styles['CommentCard__comment']}>{innerText}</p>
    </div>
  );
}
