import classNames from "classnames";
import styles from "../../assets/components/SocialMedia.module.css";
import PropTypes from "prop-types";

export default function SocialMedia({ imgUrl, linkUrl, size = "small" }) {
  const socialMediaClass = classNames(styles.icon, {
    [styles[`icon--${size}`]]: size,
  });

  return (
    <a href={linkUrl} target="_blank" rel="noopener noreferrer">
      <img className={socialMediaClass} src={imgUrl} alt="" />
    </a>
  );
}

SocialMedia.propTypes = {
  size: PropTypes.oneOf(["regular", "small"]),
};
