import styles from "../../assets/components/StarRating.module.css";
import starIcon from "../../assets/img/material-symbols_star.svg";
export default function StarRating({ rate }) {
  const stars = [];

  for (let i = 0; i < rate; i++) {
    stars.push(<img src={starIcon} alt="filled star" key={i} />);
  }
  return <div className={styles.StarRating}>{stars}</div>;
}
