import Header from "../../components/ui/Header";
import Button from "../../components/ui/Button";
import Footer from "../../components/ui/Footer";
import ServiceCard from "../../components/ui/ServiceCard";
import CommentCard from "../../components/ui/CommentCard";
import styles from "../../assets/pages/Home/Home.module.css";
import bannerImg from "../../assets/img/home__banner--img.png";
import aboutUsImg from "../../assets/img/home__aboutUs--img.png";
import weddingImg from "../../assets/img/home__services--1.png";
import partyImg from "../../assets/img/home__services--2.png";
import chidrenPartyImg from "../../assets/img/home__services--3.png";
import corporateImg from "../../assets/img/home__services--4.png";
import avatar from "../../assets/img/icon-park-solid_avatar.svg";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import CallWidget from "../../components/ui/CallWidget";

export default function Home() {
  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
      slidesToSlide: 1, // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 660 },
      items: 2,
      slidesToSlide: 1, // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 660, min: 0 },
      items: 1,
      slidesToSlide: 1, // optional, default to 1.
    },
  };

  return (
    <>
      <CallWidget />
      <Header></Header>
      <main className={styles.HomePage__container}>
        <section className={styles.HomePage__banner}>
          <div className={styles.banner__content}>
            <h1 className={styles.banner__h1}>
              Make Your Event Unforgettable with{" "}
              <span style={{ color: "#FF851B" }}>Eventify</span>
            </h1>
            <p className={styles.banner__p}>
              Professional event planning services to&nbsp;make your special day{" "}
              <span style={{ color: "#FF851B", fontStyle: "italic" }}>
                perfect
              </span>
            </p>
            <Button
              linkUrl="/services"
              innerText="Learn more"
              color="orange"
              size="regular"
            ></Button>
          </div>
          <div className={styles.banner__imgWrap}>
            <img className={styles.banner__img} src={bannerImg} alt="" />
            <img
              className={styles["banner__img--tablet"]}
              src={bannerImg}
              alt=""
            />
          </div>
        </section>
        <section className={styles.HomePage__aboutUs}>
          <img className={styles.aboutUs__img} src={aboutUsImg} alt="" />
          <div className={styles.aboutUs__content}>
            <h2 className={styles.aboutUs__h2}>Who We Are</h2>
            <p className={styles.aboutUs__p}>
              Eventify is a premier event planning company with years of
              experience in creating memorable events. From weddings to
              corporate events,{" "}
              <span style={{ color: "#FF851B", fontStyle: "italic" }}>
                we handle it all
              </span>
            </p>
          </div>
        </section>
        <section className={styles.HomePage__services}>
          <h2 className={styles.services__h2}>Our Services</h2>
          <div className={styles["services__content"]}>
            <ServiceCard
              heading="Wedding planning"
              innerText="From intimate ceremonies to&nbsp;grand celebrations, our wedding planning service ensures 
            your special day is&nbsp;perfect"
              imgURL={weddingImg}
              linkUrl="/services/weddings"
            ></ServiceCard>
            <ServiceCard
              heading="Parties"
              innerText="Create unforgettable parties with our expert planning; we&nbsp;handle every detail so&nbsp;you can celebrate fully"
              imgURL={partyImg}
              linkUrl="/services/parties"
            ></ServiceCard>
            <ServiceCard
              heading="Children&rsquo;s Parties"
              innerText="From magical themes to&nbsp;fun activities, we&nbsp;create unforgettable moments for your little one&rsquo;s special day"
              imgURL={chidrenPartyImg}
              linkUrl="/services/child-parties"
            ></ServiceCard>
            <ServiceCard
              heading="Corporate Events"
              innerText="Seamless corporate events with our expert planning, managing conferences, seminars, and team-building to&nbsp;meet your goals"
              imgURL={corporateImg}
              linkUrl="/services/corporate"
            ></ServiceCard>
          </div>
        </section>
        <section className={styles.HomePage__comments}>
          <h2 className={styles.comments__h2}>What Our Clients Say</h2>
          <div className={styles.Carousel}>
            <Carousel responsive={responsive} ssr={true} keyBoardControl={true}>
              <CommentCard
                name="James W."
                place="California, USA"
                rate={5}
                innerText="We&nbsp;hired Eventify for our annual conference, and they exceeded our expectations. Everything was organized and professional, making the event smooth and enjoyable for all attendees. Great job!"
                imgURL={avatar}
              ></CommentCard>
              <CommentCard
                name="Emily R."
                place="Ohio, USA"
                rate={5}
                innerText="Eventify truly made our wedding day a&nbsp;dream come true. From the moment we&nbsp;first met with their team, we&nbsp;knew we&nbsp;were in&nbsp;good hands. They took the time to&nbsp;understand our vision and turned it&nbsp;into a&nbsp;reality with such elegance and precision."
                imgURL={avatar}
              ></CommentCard>
              <CommentCard
                name="Sophia J."
                place="New York, USA"
                rate={5}
                innerText="Eventify planned my&nbsp;50th birthday party, and it&nbsp;was spectacular! The decorations, food, and entertainment were all top-notch. My&nbsp;guests were impressed, and&nbsp;I had the best time. Thank you!"
                imgURL={avatar}
              ></CommentCard>
              <CommentCard
                name="Michael A."
                place="Washington, USA"
                rate={5}
                innerText="Our company has used Eventify for multiple corporate events, and each time, they have surpassed our expectations. The team is&nbsp;incredibly professional, attentive, and creative. They planned our annual conference, ensuring every aspect was perfectly executed, from the keynote presentations to&nbsp;the networking sessions."
                imgURL={avatar}
              ></CommentCard>
              <CommentCard
                name="Olivia M."
                place="Florida, USA"
                rate={5}
                innerText="Eventify turned my&nbsp;daughter&rsquo;s graduation party into an&nbsp;unforgettable celebration. They handled every detail with care and creativity. The event was a&nbsp;huge success, and my&nbsp;daughter loved&nbsp;it. I&nbsp;highly recommend their services!"
                imgURL={avatar}
              ></CommentCard>
            </Carousel>
          </div>
        </section>
        <div className={styles.cta__waves}>
          <svg
            data-name="Layer 1"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 1200 120"
            preserveAspectRatio="none"
          >
            <path
              d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z"
              opacity=".25"
              className={styles["shape-fill"]}
            ></path>
            <path
              d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z"
              opacity=".5"
              className={styles["shape-fill"]}
            ></path>
            <path
              d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z"
              className={styles["shape-fill"]}
            ></path>
          </svg>
        </div>
        <section className={styles.HomePage__cta}>
          <h2 className={styles.cta__h2}>
            Ready to&nbsp;Plan Your Perfect Event?
          </h2>
          <p className={styles.cta__p}>
            Let&nbsp;us help you create unforgettable memories with our expert
            event planning services
          </p>
          <Button
            linkUrl="/contact-us"
            innerText="Get started now"
            color="white"
            size="large"
          ></Button>
        </section>
        <Footer></Footer>
      </main>
    </>
  );
}
