import styles from "../../assets/components/Input.module.css";
import classNames from "classnames";

export default function Input({
  label,
  type,
  name,
  id,
  pattern,
  required = false,
  placeholder,
  onChange,
  rounded = false,
  small = false,
}) {
  const inputClass = classNames(styles.Input, {
    [styles[`Input--round`]]: rounded,
    [styles[`Input--small`]]: small,
  });

  return (
    <label className={styles.Input__label}>
      {label}
      <input
        className={inputClass}
        type={type}
        name={name}
        id={id}
        pattern={pattern}
        required={required}
        placeholder={placeholder}
        onChange={onChange}
      />
    </label>
  );
}
