import styles from "../../assets/components/FAQCard.module.css";
import classNames from "classnames";
import arrowIcon from "../../assets/img/FAQs--arrow.svg";
import { useState, useRef } from "react";

export default function FAQCard({ questionText, answerText }) {
  const [isOpen, setIsOpen] = useState(false);

  const toggleAnswer = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className={styles.FAQCard}>
      <div className={styles.FAQCard__questionWrapper}>
        <h3
          className={classNames(styles.question, {
            [styles[`question--open`]]: isOpen,
          })}
        >
          {questionText}
        </h3>
        <button
          className={classNames(styles.plusButton, {
            [styles["plusButton--active"]]: isOpen,
          })}
          onClick={toggleAnswer}
        >
          <img className={styles.closeIcon} src={arrowIcon} alt="+" />
        </button>
      </div>
      <p
        className={classNames(styles.answer, {
          [styles[`answer--open`]]: isOpen,
        })}
      >
        {answerText}
      </p>
    </div>
  );
}
